@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background: #e3ecf6;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.nav-link {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
.nav-link:hover {
  opacity: 0.7;
  transition: 0.2s;
}
@media (max-width: 1023px) {
  .nav-link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav-link {
    display: none;
  }
}

.title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
  color: #011c40;
}
@media (max-width: 1023px) {
  .title {
    font-size: 26px;
    line-height: 150%;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 24px;
  }
}

.container {
  width: 85%;
  max-width: 1160px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    width: 88%;
  }
}
@media (max-width: 767px) {
  .container {
    width: 90%;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  min-height: 650px;
}
@media (max-width: 1023px) {
  .header {
    min-height: 638px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 500px;
  }
}

.header__background-image {
  position: absolute;
  top: 0;
  width: 100%;
  height: 650px;
  object-fit: cover;
  z-index: -1;
}
@media (max-width: 1023px) {
  .header__background-image {
    height: 638px;
  }
}
@media (max-width: 767px) {
  .header__background-image {
    height: 500px;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding-top: 21px;
  }
}

.header__logo svg {
  fill: #f9fbfd;
}
.header__logo svg:hover {
  fill: #011c40;
  transition: 0.1s;
}
@media (max-width: 1023px) {
  .header__logo svg {
    width: 38px;
    height: 20px;
  }
}

.header__nav-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 5px;
}
@media (max-width: 1023px) {
  .header__nav-list {
    align-items: start;
    padding-bottom: 0;
    margin-top: -2px;
  }
}

.header__nav-link {
  margin-left: 34px;
}
@media (max-width: 1023px) {
  .header__nav-link {
    margin-left: 23px;
  }
}

.header__title {
  margin: 190px 0 11px 95px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header__title {
    margin: 211px 0 11px 0;
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .header__title {
    margin: 128px 0 11px;
    font-size: 36px;
    line-height: 125%;
  }
}

.header__subtitle {
  margin: 0;
  margin-left: 95px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.header__subtitle br {
  display: none;
}
@media (max-width: 1023px) {
  .header__subtitle {
    margin-left: 0;
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .header__subtitle {
    font-size: 16px;
    line-height: 140%;
  }
  .header__subtitle br {
    display: block;
  }
}

.header__toggle-button {
  display: none;
  position: relative;
  width: 23px;
  height: 22px;
  cursor: pointer;
  background: inherit;
  border: none;
}
.header__toggle-button svg {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}
@media (max-width: 767px) {
  .header__toggle-button {
    display: block;
  }
}

.header__toggle-open .header__nav-list,
.header__wrapper--no-js .header__nav-list {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  width: 89%;
  height: 100%;
  background: #f9fbfd;
  padding: 75px 15px 15px 15px;
  z-index: 4;
}

.header__toggle-open .header__nav-link,
.header__wrapper--no-js .header__nav-link {
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #011c40;
  padding: 10px;
  margin-left: 0;
}

.header__toggle-open .header__nav-item,
.header__wrapper--no-js .header__nav-item {
  margin-bottom: 10px;
}

.header__toggle-open .header__logo-mobile,
.header__wrapper--no-js .header__logo-mobile {
  position: absolute;
  top: 21px;
  left: 20%;
}

.header__wrapper--no-js .header__logo-mobile {
  display: block;
}

.header__toggle-button-close,
.header__logo-mobile {
  display: none;
  z-index: 6;
}

.header__toggle-open .header__toggle-button-close,
.header__toggle-open .header__logo-mobile {
  display: block;
}

.traveling {
  background-color: #ffffff;
  padding: 120px 0 145px 0;
}
@media (max-width: 1023px) {
  .traveling {
    padding: 100px 0 93px;
  }
}
@media (max-width: 767px) {
  .traveling {
    padding: 66px 0 85px;
  }
}

.traveling__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .traveling__wrapper {
    flex-direction: column;
  }
}

.traveling__description {
  width: 45%;
}
@media (max-width: 1023px) {
  .traveling__description {
    width: 48%;
  }
}
@media (max-width: 767px) {
  .traveling__description {
    width: 100%;
    margin-bottom: 44px;
  }
}

.traveling__description-title {
  margin: 56px 0 24px;
}
@media (max-width: 1023px) {
  .traveling__description-title {
    margin: -9px -1px 15px;
    line-height: 140%;
  }
}

.traveling__description-info {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #011c40;
}

.traveling__content-image {
  width: 560px;
  height: 301px;
  margin-left: 20px;
}
@media (max-width: 1023px) {
  .traveling__content-image {
    width: 324px;
    height: 174.15px;
    margin-right: 4px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .traveling__content-image {
    width: 290px;
    height: 155.88px;
    margin: auto;
  }
}

.advantage {
  position: relative;
  padding: 110px 0 118px;
}
@media (max-width: 1023px) {
  .advantage {
    padding: 90px 0 100px;
  }
}
@media (max-width: 767px) {
  .advantage {
    padding: 57px 0 67px;
  }
}

.advantage__background-image {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.advantage__title {
  margin: 0;
  text-align: center;
  margin-bottom: 49px;
}
@media (max-width: 767px) {
  .advantage__title {
    margin-bottom: 30px;
  }
}

.advantage__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
}
@media (max-width: 1023px) {
  .advantage__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantage__list {
    grid-template-columns: 1fr;
  }
}

.advantage__item {
  background: #f9fbfd;
  padding: 30px 30px 46px 30px;
}
@media (max-width: 1023px) {
  .advantage__item {
    padding: 26px 16px 20px 18px;
  }
}
@media (max-width: 767px) {
  .advantage__item {
    padding: 25px 16px 19px;
  }
}
.advantage__item svg {
  fill: #011c40;
  margin-bottom: 11px;
}
@media (max-width: 1023px) {
  .advantage__item svg {
    height: 26px;
  }
}
@media (max-width: 767px) {
  .advantage__item svg {
    margin-bottom: 13px;
  }
}

@media (max-width: 1023px) {
  .advantage__item:last-child .advantage__item-subtitle {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .advantage__item:last-child .advantage__item-subtitle {
    margin-bottom: 11px;
  }
}

.advantage__item-subtitle {
  margin: 0;
  margin-bottom: 10px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #011c40;
}
.advantage__item-subtitle br {
  display: none;
}
@media (max-width: 1023px) {
  .advantage__item-subtitle {
    line-height: 140%;
  }
}
@media (max-width: 767px) {
  .advantage__item-subtitle {
    margin-left: 3px;
    margin-bottom: 8px;
  }
  .advantage__item-subtitle br {
    display: block;
  }
}

.advantage__item-description {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #011c40;
}
@media (max-width: 1023px) {
  .advantage__item-description {
    line-height: 143%;
  }
  .advantage__item-description br {
    display: none;
  }
}
@media (max-width: 767px) {
  .advantage__item-description {
    margin-left: 3px;
  }
}

.cruises {
  background-color: #ffffff;
  padding: 87px 0 120px;
}
@media (max-width: 1023px) {
  .cruises {
    padding: 90px 0 100px;
  }
}
@media (max-width: 767px) {
  .cruises {
    padding: 56px 0 65px;
  }
}

.cruises .container {
  width: 83%;
}
@media (max-width: 1023px) {
  .cruises .container {
    width: 84%;
  }
}

.cruises__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 30px;
}
@media (max-width: 1023px) {
  .cruises__list {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .cruises__list {
    grid-template-columns: 1fr;
  }
}

.cruises__item {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  min-height: 386px;
}
.cruises__item h3 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #f9fbfd;
  margin: 0;
}

.cruises__item-front-title {
  position: absolute;
  bottom: 6%;
  left: 12%;
  margin: 0;
}
@media (max-width: 1023px) {
  .cruises__item-front-title {
    bottom: 27px;
    left: 33px;
  }
}
@media (max-width: 767px) {
  .cruises__item-front-title {
    bottom: 17px;
    left: 24px;
  }
}

.cruises__title {
  text-align: center;
  margin: 0;
  margin-bottom: 49px;
}
@media (max-width: 767px) {
  .cruises__title {
    margin-bottom: 30px;
  }
}

.cruises__item-front-image {
  width: 100%;
  height: 386px;
}

.cruises__item-back {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: 1s;
  z-index: 1;
  padding: 20px;
  background: #011c40;
  box-shadow: 0 4px 35px rgba(1, 28, 64, 0.15);
  top: 100%;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cruises__item:hover .cruises__item-back {
  top: 0%;
}

.cruises__item-back-description {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #d8d8d9;
  margin: 0;
}
.cruises__item-back-description span {
  color: #f9fbfd;
}

.cruises__item-back-button {
  width: 100%;
  height: 40px;
  background: #f9fbfd;
  border: none;
  cursor: pointer;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #011c40;
}

